body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  --primary: rgb(240, 205, 205);
  --secondary: rgb(118, 118, 214);
  --darSecondary: rgb(102, 73, 231);
  --white: rgb(239, 235, 235);
  --br: 5px;
  --pd: 5px;
  --gap: 5px;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  width: 55%;

  background-color: antiquewhite;
  display: grid;
  grid-template: auto / 1.5fr 2fr;
  border-radius: calc(var(--br) * 3);
}

button {
  display: none;
  position: relative;
  padding: var(--pd);
  border-radius: var(--br);
  border: 0;
  background-color: var(--secondary);
  color: var(--white);
  width: 50px;
  height: 30px;
  cursor: pointer;
  top: 20px;
  align-items: center;
  justify-content: center;
}
.contact:hover button,
.skills:hover button,
.interests:hover button,
.profile:hover button,
.projects:hover button,
.education:hover button {
  display: flex;
}
button:hover {
  background-color: var(--darSecondary);
}
.left {
  grid-template-columns: 1/2;
  /* background-color: var(--primary); */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: calc(var(--pd) * 2);
}
.contact,
.skills,
.interests,
.profile,
.projects,
.education {
  padding: var(--pd);
  display: flex;
}
ul,
p,
h3,
h4 {
  text-align: start;
}

img {
  width: 80px;
  border-radius: 50%;
  cursor: pointer;
}

.profileImg {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 70px;
}
.right {
  grid-template-columns: 2/3;
  /* background-color: var(--secondary); */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.profile {
  padding: calc(var(--pd) * 2);
  text-align: left;
}
.main {
  padding-left: 50px;
  padding-right: 15px;
  text-align: left;
}

.head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

form {
  width: 300px;
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: var(--primary);
  border-radius: var(--br);
  align-items: center;
  justify-content: center;
  grid-gap: var(--gap);
  gap: var(--gap);
  padding: var(--pd);
}

form input {
  border-radius: calc(var(--br) * 0.5);
  border: 0;
}

form button {
  width: 100px;
  right: 0;
  top: 0;
}
.formButtons {
  position: relative;
  display: flex;
  justify-content: space-around;
  grid-gap: calc(var(--gap) * 3);
  gap: calc(var(--gap) * 3);
}
#profileImgFormButton, #closeButtonProfileImgFormButton{
  width: 100px;
  position: relative;
  display: flex;
}
